module.exports = function (ints, inputs) {
  var outputs = [];
  var shouldExit = false;
  var index = 0;

  while (!shouldExit) {
    var commandInt = ints[index];

    if (commandInt === "99") {
      return outputs;
    }

    var command = getCommand(String(commandInt));

    if (command.command) {
      index = command.command(ints, inputs, outputs, command.paramModes, index);
    } else {
      return;
    }
  }
};

var add = function add(ints, _, __, paramModes, index) {
  var start = Number(ints[++index]);
  var end = Number(ints[++index]);
  var position = Number(ints[++index]);
  var param1 = paramModes[0] === 0 ? Number(ints[start]) : start;
  var param2 = paramModes[1] === 0 ? Number(ints[end]) : end;
  ints[position] = Number(param1) + Number(param2);
  return index + 1;
};

var multiply = function multiply(ints, _, __, paramModes, index) {
  var start = Number(ints[++index]);
  var end = Number(ints[++index]);
  var position = Number(ints[++index]);
  var param1 = paramModes[0] === 0 ? Number(ints[start]) : start;
  var param2 = paramModes[1] === 0 ? Number(ints[end]) : end;
  ints[position] = Number(param1) * Number(param2);
  return index + 1;
};

var input = function input(ints, inputs, _, __, index) {
  var saveIndex = Number(ints[++index]);
  var input = inputs.pop();
  ints[saveIndex] = input;
  return index + 1;
};

var output = function output(ints, _, outputs, paramModes, index) {
  var readIndex = Number(ints[++index]);
  var toOutput = paramModes[0] === 0 ? ints[readIndex] : readIndex;
  outputs.push(toOutput);
  return index + 1;
};

var jumpTrue = function jumpTrue(ints, inputs, outputs, paramModes, index) {
  var checker = Number(ints[++index]);
  var shouldJump = paramModes[0] === 0 ? Number(ints[checker]) !== 0 : checker !== 0;

  if (shouldJump) {
    var newIndexIndex = Number(ints[++index]);
    var newIndex = paramModes[1] === 0 ? Number(ints[newIndexIndex]) : newIndexIndex;
    return newIndex;
  }

  return index + 2;
};

var jumpFalse = function jumpFalse(ints, inputs, outputs, paramModes, index) {
  var checker = Number(ints[++index]);
  var shouldJump = paramModes[0] === 0 ? Number(ints[checker]) === 0 : checker === 0;

  if (shouldJump) {
    var newIndexIndex = Number(ints[++index]);
    var newIndex = paramModes[1] === 0 ? Number(ints[newIndexIndex]) : newIndexIndex;
    return newIndex;
  }

  return index + 2;
};

var lessThan = function lessThan(ints, inputs, outputs, paramModes, index) {
  var firstIndex = Number(ints[++index]);
  var secondIndex = Number(ints[++index]);
  var first = paramModes[0] === 0 ? Number(ints[firstIndex]) : firstIndex;
  var second = paramModes[1] === 0 ? Number(ints[secondIndex]) : secondIndex;
  var location = Number(ints[++index]);
  var toStore = first < second ? 1 : 0;
  ints[location] = toStore;
  return index + 1;
};

var equals = function equals(ints, inputs, outputs, paramModes, index) {
  var firstIndex = Number(ints[++index]);
  var secondIndex = Number(ints[++index]);
  var first = paramModes[0] === 0 ? Number(ints[firstIndex]) : firstIndex;
  var second = paramModes[1] === 0 ? Number(ints[secondIndex]) : secondIndex;
  var location = Number(ints[++index]);
  var toStore = first === second ? 1 : 0;
  ints[location] = toStore;
  return index + 1;
};

var commands = {
  1: add,
  2: multiply,
  3: input,
  4: output,
  5: jumpTrue,
  6: jumpFalse,
  7: lessThan,
  8: equals
};

var getCommand = function getCommand(commandString) {
  var commandInt = commandString.slice(-2);
  var param1 = 0,
      param2 = 0,
      param3 = 0;

  if (commandString.length > 2) {
    param1 = Number(commandString.slice(-3, -2));
  }

  if (commandString.length > 3) {
    param2 = Number(commandString.slice(-4, -3));
  }

  if (commandString.length > 4) {
    param3 = Number(commandString.slice(-5, -4));
  }

  return {
    command: commands[Number(commandInt)],
    paramModes: [param1, param2, param3]
  };
};